                Liaoning Technical University
            </div>
        </div>
        <vue-baberrage  :isShow="isBarrageShow" id="baberrage" :throttleGap="5000" :barrageList="tagList" :loop="isBarrageLoop" :messageHeight="messageHeight" :boxHeight="boxHeight"></vue-baberrage>
    </div>
</template>

<script>
    import {MESSAGE_TYPE} from 'vue-baberrage'
    export default {
        name: 'Head',
        components: {},
        data(){
            return {
                tagList: [],
                isBarrageShow: true,
                boxHeight: 200,
                messageHeight: 40,
                isBarrageLoop: true,
                barageIconList:[
                    'http://nsimg.cn-bj.ufileos.com/barfire.png',
                    'http://nsimg.cn-bj.ufileos.com/barangel.png',
                    'http://nsimg.cn-bj.ufileos.com/bardevil.png',
                    'http://nsimg.cn-bj.ufileos.com/barflower.png',
                    'http://nsimg.cn-bj.ufileos.com/barsprite.png'
                ],
                barageMessageList: [
                    '沙雕大学生',
                    '女朋友占据娱乐圈的半壁江山(另外半壁是男明星)',
                    '张子枫是我媳妇儿',
                    '闲不住 喜欢瞎鼓捣',
                    '真的不喜欢玩游戏啊',
                    'BugMaker',
                    '喜欢Hiphop',
                    '手残志坚的Ukulele弹奏者',
                    '技术圈外默默无闻的小菜鸡',
                    '给台电脑能宅到死',
                    '就算没有新冠病毒也不外出',
                    '不管哪一年问我年龄我都会说18',
                ]
            }
        },
        methods :{
            goPage(url){
                window.location.href = url
            },
            randomItem(arr){
                return arr[Math.floor(Math.random() * arr.length)]
            },
            addBarage(){
                for(let i=0;i<this.barageMessageList.length;i++){
                    this.tagList.push({
                        id: i,
                        msg: this.barageMessageList[i],
                        avatar: this.randomItem(this.barageIconList),
                        time: 20 + Math.floor(Math.random() * 5),
                        type: MESSAGE_TYPE.NORMAL
                    })
                }
            }
        },
        mounted() {
            this.addBarage()
        }
    }
</script>

